import React from 'react'
import styled from 'styled-components'
import { useVaultUser } from 'state/hooks'
import { useTranslation } from 'contexts/Localization'
import { Text } from 'archerswap-uikit'
import { getBalanceNumber } from 'utils/formatBalance'

export interface VaultProps {
  label: string
  pid: number
  image: string
  provider?: string
}

const IconImage = styled.div`
  display: flex;
  margin-right: 15px;
  ${({ theme }) => theme.mediaQueries.md} {
    margin-right: 5px;
  }
  .first {
    position: relative;
    display: block;
    border-radius: 50%;
    z-index: 2;
    width: 20px;
    height: 20px;

    ${({ theme }) => theme.mediaQueries.sm} {
      width: 30px;
      height: 30px;
    }
  }
  .last {
    z-index: 3;
    display: block;
    margin-left: -7px;
    border-radius: 50%;
    width: 20px;
    height: 20px;

    ${({ theme }) => theme.mediaQueries.sm} {
      width: 30px;
      height: 30px;
      margin-left: -12px;
    }
  }
`

const Container = styled.div`
  padding-left: 16px;
  display: flex;
  align-items: center;

  ${({ theme }) => theme.mediaQueries.sm} {
    padding-left: 32px;
  }
`

const Vault: React.FunctionComponent<VaultProps> = ({ label, pid, provider }) => {
  const { stakedBalance } = useVaultUser(pid, provider)
  const { t } = useTranslation()
  const rawStakedBalance = getBalanceNumber(stakedBalance)

  const handleRenderVaulting = (): JSX.Element => {
    if (rawStakedBalance) {
      return (
        <Text color="secondary" fontSize="12px" bold>
          {t('Staked')}
        </Text>
      )
    }

    return null
  }

  return (
    <Container>
      {/* <IconImage src={`/images/farms/${image}.svg`} alt="icon" width={40} height={40} mr="8px" /> */}
      <IconImage>
        <img className="first" src={`/images/tokens/${label.split('-')[0].toLocaleLowerCase()}.png`} alt="icon" />
        <img className="last" src={`/images/tokens/${label.split('-')[1].toLocaleLowerCase()}.png`} alt="icon" />
      </IconImage>
      <div>
        {handleRenderVaulting()}
        <Text bold>{label}</Text>
      </div>
    </Container>
  )
}

export default Vault
